
import './App.css';
import Main from './componentd/main'
import React from "react";
// 配置路由
import {Switch,Route} from 'react-router-dom'
import {mainRouter} from './routes/index'
import SilderInfo from "./componentd/silderInfo";
function App() {
  return (
      <>
          <Switch>
              {mainRouter.map((route,i)=> {
                  return <Route key={i} path={route.path} exact={route.exact} render={routeProps => {
                      return(
                          <Main
                            content={
                                <route.component key={i} {...routeProps}/>
                            }
                            sidebar={
                                <SilderInfo type={route.side}/>
                            }
                          />
                      )
                  }
                  }/>
              })}
          </Switch>
      </>
  );
}

export default App;
