import React, {Component} from 'react';
import { Layout,Menu } from 'antd';
import {withRouter} from 'react-router-dom'
import {mainRouter} from "../routes";
import { createFromIconfontCN } from '@ant-design/icons';
const IconFont = createFromIconfontCN({
    scriptUrl: '//at.alicdn.com/t/font_2463236_whv0btsk0la.js',
});
const { Header, Footer, Sider, Content} = Layout;
const routers = mainRouter.filter(value => value.isShow)
class Main extends Component {
    // eslint-disable-next-line no-useless-constructor
    constructor(props) {
        super(props);
    }
    state = {
        current: '',
    }
    // 组件创建后执行
    componentDidMount() {
        this.tabChange()
    }
    tabChange =()=>{
        let arr = this.props.history.location.pathname.split('/')
        // 修改tab选中状态以及路由跳转
        this.setState({ current: arr[arr.length-1] });
    }
    render() {
        const { current } = this.state;
        return (
            <Layout className="layout">
                <Sider className='sider' width='250px'>
                    {this.props.sidebar}
                </Sider>
                <Layout >
                    <Header className='header'>
                        <Menu  mode="horizontal"  selectedKeys={[current]} >
                            {/*循环生成菜单*/}
                            {
                                routers.map(route => {
                                    return (
                                        <Menu.Item key={route.key} onClick={p => this.props.history.push(route.path)}>
                                            <IconFont type={route.icon?route.icon:" "}/>
                                            {route.name}
                                        </Menu.Item>
                                    )
                                })
                            }

                        </Menu>
                    </Header>
                    <Content style={{backgroundColor:'#fff'}}>
                        {this.props.content}

                    </Content>

                    <Footer className='footer'>Footer</Footer>
                </Layout>
            </Layout>
        );
    }
}

export default withRouter(Main);
